import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { v4 as uuidv4 } from 'uuid';
import {
  Box,
  Button,
  Container,
  Card,
  Grid,
  Typography,
  TextField,
  makeStyles
} from '@material-ui/core';
import { getReports } from 'src/actions/reportActions';
import { HEADING_OPTIONS, GROUP_HEADINGS } from 'src/constants';

function HeadingDetail({ data, updateData }) {
  const dispatch = useDispatch();
  const [saving, setSaving] = useState(false);
  const [heading, setHeading] = useState(data);
  const [subHeading, setSubHeading] = useState(null);

  useEffect(() => {
    setHeading(data);
  }, [data]);

  if (!data) {
    return null;
  }

  const newSubHeading = () => {
    const id = uuidv4();
    setSubHeading({ id, label: '' });
  };

  const handleChangeHeadingLabel = (event) => {
    setHeading({ ...heading, label: event.target.value });
  };

  const addSubHeading = () => {
    if (heading && heading.children) {
      heading.children.push(subHeading);
    } else {
      heading.children = [subHeading];
    }
    updateData(heading);
  };

  const handleChangeSubHeading = (event) => {
    setSubHeading({ ...subHeading, label: event.target.value });
  };

  return (
    <>
      {heading && (
      <Container>
        <Box mt={3}>
          {saving
            ? (
              <TextField
                onChange={(e) => { handleChangeHeadingLabel(e); }}
                value={heading.label}
              />
            ) : (
              <Typography
                color="textPrimary"
                variant="h5"
              >
                {heading.label}
              </Typography>
            )}
        </Box>
          {heading.subHeading ? (
            <>
              {saving ? (
                <Autocomplete
                  id="grouped-demo"
                  options={HEADING_OPTIONS}
                  groupBy={(option) => option.group}
                  getOptionLabel={(option) => option.title}
                  style={{ width: 300 }}
                  renderInput={(params) => <TextField {...params} label="With categories" variant="outlined" />}
                />
              ) : (
                <Typography
                  color="textPrimary"
                  variant="h5"
                >
                  {subHeading.label}
                </Typography>
              )}
            </>
          ) : (
            <>
              <Button
                color="secondary"
                variant="contained"
                size="small"
                onClick={() => { newSubHeading(); }}
              >
                New SubHeading
              </Button>
              {subHeading && (
              <Card>
                <TextField
                  fullWidth
                  label="Sub Heading"
                  margin="normal"
                  name="subHeading"
                  onChange={handleChangeSubHeading}
                  value={subHeading.label || ''}
                  variant="outlined"
                />
                <Button
                  color="secondary"
                  onClick={() => { addSubHeading(); }}
                  type="submit"
                  variant="contained"
                  size="large"
                >
                  Add
                </Button>
              </Card>
              )}
            </>
          )}
        {saving
          ? (
            <Button
              color="secondary"
              variant="contained"
              size="small"
              onClick={() => { updateData(heading); }}
            >
              Save
            </Button>
          ) : (
            <Button
              color="secondary"
              variant="contained"
              size="small"
              onClick={() => { setSaving(true); }}
            >
              Edit
            </Button>
          )}
      </Container>
      )}
    </>
  );
}

HeadingDetail.propTypes = {
  data: PropTypes.object,
  updateData: PropTypes.func
};

export default HeadingDetail;
