/* eslint-disable consistent-return */
import React, { useState } from 'react';
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
import axios from 'src/utils/axios';
import {
  Box,
  Button,
  Container,
  Grid,
  TextField,
  makeStyles
} from '@material-ui/core';
import { useSnackbar } from 'notistack';
import { v4 as uuidv4 } from 'uuid';
import Page from 'src/components/Page';
import { API_BASE_URL } from 'src/config';
import { REPORT } from 'src/config/endpoint';
import Header from './Header';
import HeadingDetail from './HeadingDetail';

const useStyles = makeStyles({
  root: {
    height: 216,
    flexGrow: 1,
    maxWidth: 500,
  },
  container: {

  },
  panel: {
    height: 500,
  },
  treeView: {

  },
  treeItem: {
    witdh: 300,
  }
});

function ReportCreateView() {
  const classes = useStyles();
  const [expanded, setExpanded] = useState([]);
  const [selected, setSelected] = useState(null);
  const [title, setTitle] = useState('');
  const [headings, setHeadings] = useState([]);
  const [selHeading, setSelHeading] = useState(null);
  const { enqueueSnackbar } = useSnackbar();
  //  const { reports } = useSelector((state) => (state.report));

  const handleToggle = (event, nodeIds) => {
    setExpanded(nodeIds);
  };

  const handleSelect = (event, nodeId) => {
    setSelected(nodeId);
    headings.some((heading) => {
      if (heading.id === nodeId) {
        setSelHeading(heading);
      } else if (heading.children) {
        heading.children.some((child) => {
          if (child.id === nodeId) {
            setSelHeading({ ...child, subHeading: true });
          }
        });
      }
    });
  };

  const handleCreateHeading = () => {
    setHeadings([...headings, { id: uuidv4(), label: 'New Heading' }]);
  };

  const handleCreateReport = () => {
    axios.post(`${API_BASE_URL}/${REPORT.CREATE}`, {
      title,
      headings,
    })
      .then(() => {
        enqueueSnackbar('Report created', {
          variant: 'success',
          action: <Button onClick={() => { console.log('created'); }}>See all</Button>
        });
      });
  };

  const updateHeading = (data) => {
    headings.forEach((heading, index) => {
      if (heading.id === data.id) {
        headings[index] = data;
        setHeadings([...headings]);
      } else if (heading.children) {
        heading.children.some((child, indexChild) => {
          if (child.id === data.id) {
            headings[index].children[indexChild] = data;
            setHeadings([...headings]);
          }
        });
      }
    });
    setSelHeading(data);
  };

  return (
    <Page
      className={classes.root}
      title="Project Create"
    >
      <Container
        className={classes.container}
        maxWidth="lg"
        pl={2}
        mt={2}
      >
        <Header />
        <Box>
          <TextField
            fullWidth
            label="Heading Title"
            margin="normal"
            name="title"
            onChange={(e) => { setTitle(e.target.value); }}
            value={title}
            variant="outlined"
          />
        </Box>
        <Box
          className={classes.panel}
          mt={3}
          display="flex"
        >
          <Grid>
            <TreeView
              className={classes.treeView}
              defaultCollapseIcon={<ExpandMoreIcon />}
              defaultExpandIcon={<ChevronRightIcon />}
              expanded={expanded}
              selected={selected}
              onNodeToggle={handleToggle}
              onNodeSelect={handleSelect}
            >
              {headings.map((heading) => (
                <TreeItem
                  className={classes.treeItem}
                  key={heading.id}
                  nodeId={heading.id}
                  label={heading.label}
                >
                  { heading.children && (heading.children.map((subheading) => (
                    <TreeItem
                      className={classes.treeItem}
                      key={subheading.id}
                      nodeId={subheading.id}
                      label={subheading.label}
                    />
                  )))}
                </TreeItem>
              ))}
            </TreeView>
          </Grid>
          {selHeading && (
          <Grid>
            <HeadingDetail
              data={selHeading}
              updateData={updateHeading}
            />
          </Grid>
          )}
        </Box>
        <Box
          mt={3}
          display="flex"
        >
          <Button
            color="secondary"
            variant="contained"
            size="small"
            onClick={handleCreateHeading}
          >
            Create Heading
          </Button>
          <Button
            color="secondary"
            variant="contained"
            size="small"
            onClick={handleCreateReport}
          >
            Create
          </Button>
        </Box>
      </Container>
    </Page>
  );
}

export default ReportCreateView;
